<template>
  <!-- 네비게이션바 -->
  <C_NavBar />
  <!-- 이미지바 -->
  <C_ImageBar v-if="imagebar == true" />
  <!-- 사이드바와 컨텐츠 -->
  <div class="main-content-container">
    <C_SideBar v-if="imagebar == true" />
    <router-view ></router-view>
  </div>

  <!-- 하단바 -->
  <C_NavBar2 />
  <C_BottomBar />
</template>

<script>
import C_NavBar from "./components/product/C_NavBar.vue";
import C_ImageBar from "./components/product/C_ImageBar.vue";
import C_NavBar2 from "./components/product/C_NavBar2.vue";
import C_BottomBar from "./components/product/C_BottomBar.vue";
import C_SideBar from "./components/product/C_SideBar.vue";
import { mapState } from "vuex";

export default {
  name: "App",

  created() {
    const savedState = localStorage.getItem("vuex-state");
    if (savedState) {
      this.$store.replaceState(JSON.parse(savedState));
    }
  },

  computed: {
    ...mapState(["imagebar"]),
  },

  components: {
    C_NavBar,
    C_ImageBar,
    C_NavBar2,
    C_BottomBar,
    C_SideBar,
  },
};
</script>

