import { createWebHistory, createRouter } from "vue-router";
import R_Error404 from './components/R_Error404'
import R_Home from './components/R_Home'
import R_Info from './components/R_Info'
import R_Sermon from './components/R_Sermon'
import R_News from './components/R_News'
import R_Education from './components/R_Education'

const routes = [
  {
    path: "/",
    component: R_Home,
  },
  {
    path: "/info",
    component: R_Info,
  },
  {
    path: "/sermon",
    component: R_Sermon,
  },
  {
    path: "/news",
    component: R_News,
  },
  {
    path: "/education",
    component: R_Education,
  },
  // {
  //   //\\d+ 숫자만 정규식 문법
  //   path: "/detail/:id(\\d+)",
  //   component: C_Detail,
  //   //nested routes
  //   children: [
  //     {
  //       path: "author",
  //       component: C_Author,
  //     },
  //     {
  //       path: "comment",
  //       component: C_Comment,
  //     },
  //   ]
  // },
  {
    //이외의 페이지 오류페이지 처리
    path: "/:anything(.*)",
    component: R_Error404,
  },

];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 