<template>
  <!-- Sidebar -->
  <nav class="leftbar">
      <ul class="nav flex-column" style="width: 11rem;">
        <li v-for="(a, i) in menuItems" :key="i" class="nav-item">
          <button
            class="nav-link"
            :class="{
              active: a.active,
              'custom-active': a.active,
            }"
            @click="toggleMenuItem(a), setMenuStep(i)"
          >
            {{ a.label }}
          </button>
        </li>
      </ul>
  </nav>
</template>

<script>
import { mapMutations, mapState } from "vuex";
export default {
  name:"C_SideBar",
  computed :{
    ...mapState(["menuItems"])
  },
  methods: {
    ...mapMutations(["setMenuStep","setMenuItems"]),
    toggleMenuItem(menuItemToToggle) {
      // 현재 클릭된 항목의 "active" 상태를 확인
      if (menuItemToToggle.active) {
        return; // 이미 활성화된 항목을 다시 클릭하면 아무것도 하지 않음
      }

      // 클릭된 항목의 "active" 상태를 토글
      menuItemToToggle.active = !menuItemToToggle.active;

      // 다른 모든 항목의 "active" 속성을 해제
      this.menuItems.forEach((a) => {
        if (a !== menuItemToToggle) {
          a.active = false;
        }
      });
    },
  }
};
</script>

<style scoped>
/* Adjust sidebar styling */
.leftbar {
  background-color: #f8f9faff;
  height: 100%;
  margin-top: 3rem;
  margin-bottom: 1rem;
  padding: 0px;
  max-width: 15rem;
}

.nav-link {
  color: black;
  width: 100%;
  font-size: 1.2em;
}
.nav-link.active {
  background-color: #545454;
  color: #fff;
}


@media (max-width: 1200px) {
  .leftbar {
    zoom: 0.8
}
}
@media (max-width: 767.5px) {
  .leftbar {
    zoom: 0.7
}
}
@media (max-width: 576px) {
  .leftbar {
    zoom: 0.6
}
}

</style>