<template>
  <div class="main-content">
    <!-- Main content -->
    <main>
      <R_Menu0 v-if="menuStep == 0" />
      <R_Menu1 v-if="menuStep == 1" />
      <R_Menu2 v-if="menuStep == 2" />
      <R_Menu3 v-if="menuStep == 3" />
      <R_Menu4 v-if="menuStep == 4" />
      <R_Menu5 v-if="menuStep == 5" />
      <R_Menu6 v-if="menuStep == 6" />
      <R_Menu7 v-if="menuStep == 7" />
    </main>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import R_Menu0 from "./InfoMenu/R_Menu0.vue";
import R_Menu1 from "./InfoMenu/R_Menu1.vue";
import R_Menu2 from "./InfoMenu/R_Menu2.vue";
import R_Menu3 from "./InfoMenu/R_Menu3.vue";
import R_Menu4 from "./InfoMenu/R_Menu4.vue";
import R_Menu5 from "./InfoMenu/R_Menu5.vue";
import R_Menu6 from "./InfoMenu/R_Menu6.vue";
import R_Menu7 from "./InfoMenu/R_Menu7.vue";

export default {
  name: "C_Info",
  data() {
    return {
      infoItems: [
        { label: "비전과 로고" },
        { label: "역사" },
        { label: "담임목사" },
        { label: "오시는길" },
        { label: "섬기는이" },
        { label: "예배안내" },
        { label: "새가족등록안내" },
        { label: "헌금·후원안내" },
      ],
    };
  },
  created() {
    this.setMenuItems(this.infoItems);
  },
  mounted() {
    this.setImagebar("sub");
    this.menuItems[this.menuStep].active = true;
  },
  computed: {
    ...mapState(["menuStep", "menuItems"]),
  },

  methods: {
    ...mapMutations(["setImagebar", "setInfoStep", "setMenuItems"]),

    toggleMenuItem(menuItemToToggle) {
      // 현재 클릭된 항목의 "active" 상태를 확인
      if (menuItemToToggle.active) {
        return; // 이미 활성화된 항목을 다시 클릭하면 아무것도 하지 않음
      }

      // 클릭된 항목의 "active" 상태를 토글
      menuItemToToggle.active = !menuItemToToggle.active;

      // 다른 모든 항목의 "active" 속성을 해제
      this.menuItems.forEach((a) => {
        if (a !== menuItemToToggle) {
          a.active = false;
        }
      });
    },
  },
  components: {
    R_Menu0,
    R_Menu1,
    R_Menu2,
    R_Menu3,
    R_Menu4,
    R_Menu5,
    R_Menu6,
    R_Menu7,
  },
};
</script>

<style scoped>
.main-content {
  width: 100%;
  margin-left: 1rem;
}

</style>